const animations = [];

const quickSort = (arr) => {
    sort(arr, 0, arr.length - 1);
    return animations;
}

const sort = (arr, left, right) => {
    let index;
    if (arr.length > 1) {
        index = partition(arr, left, right); //index returned from partition
        if (left < index - 1) { //more elements on the left side of the pivot
            sort(arr, left, index - 1);
        }
        if (index < right) { //more elements on the right side of the pivot
            sort(arr, index, right);
        }
    }
    return arr;
}

const partition = (arr, left, right) => {
    let pivot = arr[Math.floor((right + left) / 2)], //middle element
        i = left, //left pointer
        j = right; //right pointer
    while (i <= j) {
        while (arr[i] < pivot) {
            i++;
        }
        while (arr[j] > pivot) {
            j--;
        }
        if (i <= j) {
            swap(arr, i, j); //sawpping two elements
            i++;
            j--;
        }
    }
    return i;
}

const swap = (arr, leftIndex, rightIndex) => {
    const temp = arr[leftIndex];
    arr[leftIndex] = arr[rightIndex];
    arr[rightIndex] = temp;
    animations.push({
        cols: [leftIndex, rightIndex],
        swap: true
    });
}

export default quickSort;