const animations = [];
const heapSort = (arr) => {
    let size = arr.length;
    for (let i = Math.floor(size / 2); i >= 0; i--) {
        heapify(arr, size, i);
    }

    for (let i = arr.length - 1; i > 0; i--) {
        swap(arr, 0, i);
        size--;
        heapify(arr, i, 0);
    }

    return animations;
}

const heapify = (arr, n, i) => {
    const left = 2 * i + 1;
    const right = 2 * i + 2;
    let max = i;

    if (left < n && arr[left] > arr[max]) {
        max = left;
    }

    if (right < n && arr[right] > arr[max]) {
        max = right;
    }

    if (max !== i) {
        swap(arr, i, max);
        heapify(arr, n, max);
    }
}

const swap = (arr, index_A, index_B) => {
    animations.push({
        cols: [index_A, index_B],
        swap: true
    });
    const temp = arr[index_A];

    arr[index_A] = arr[index_B];
    arr[index_B] = temp;
}

export default heapSort;