const shellSort = (arr) => {

    let inc = arr.length / 2;
    while (inc > 0) {
        for (let i = inc; i < arr.length; i++) {
            let j = i;
            let temp = arr[i];

            while (j >= inc && arr[j - inc] > temp) {
                arr[j] = arr[j - inc];
                j = j - inc;
            }

            arr[j] = temp;
        }

        if (inc === 2) {
            inc = 1;
        } else {
            inc = parseInt(inc * 5 / 11);
        }
    }
    return arr;

}

export default shellSort;